* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

html,
body {
   width: 100%;
   height: 100%;
}

img,
video {
   max-width: 100%;
   height: auto;
}

ul,
ol {
   list-style: none;
}

a {
   text-decoration: none;
   color: inherit;
   display: block;
}

a,
button,
input,
textarea,
select,
div,
span {
   outline: none !important;
   -webkit-tap-highlight-color: transparent; /* Убирает подсветку при тапе на мобильных */
}

button,
input,
textarea {
   font: inherit;
   border: none;
   background: none;
   padding: 0;
   outline: none;
}

button {
   cursor: pointer;
   transition: all 0.3s;
}
