   &__zipper--left {
      position: absolute;
      top: 17px;
      left: 20px;
      width: 34px;
      height: 82px;
      background-image: url('../assets/img/we-know/lighthing.svg');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: rotate(-35deg);
   }
   &__zipper--right {
      position: absolute;
      top: 17px;
      right: 25px;
      width: 34px;
      height: 82px;
      background-image: url('../assets/img/we-know/lighthing.svg');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: rotate(35deg);
   }

   &__title {
      position: absolute;
      top: 32%;
      left: -49%;
      font-family: var(--font3-1);
      font-weight: 400;
      font-size: 64px;
      line-height: 127%;
      text-align: center;
      p {
         margin-bottom: -18px;
         color: var(--white);
      }
      span {
         display: inline-block;
         color: transparent;
         font-family: var(--font3-3);
         font-weight: 600;
         font-size: 64px;
         line-height: 127%;
         text-align: center;
         background: linear-gradient(
            90deg,
            #de31ef 0%,
            #d0b4f0 69%,
            #de31ef 100%
         );
         background-clip: text;
         background-size: 100%;
         -webkit-background-clip: text;
         -webkit-text-fill-color: transparent;
         mix-blend-mode: color-dodge;
         white-space: nowrap;
      }
   }

   &__list-item {
      font-family: var(--font-family);
      font-weight: 400;
      color: var(--white);
      position: absolute;

      z-index: 20;
      line-height: 120%;
      &:nth-child(1) {
