.you-need {
   margin-bottom: 85px;
   &__wrapper {
   }

   &__title {
      text-align: center;
      margin-bottom: 87px;
   }
   @media (max-width: 1240px) {
      margin-bottom: 40px;
   }
   // @media (max-width: 1024px) {
   //    margin-bottom: 40px;
   // }
   @media (max-width: 650px) {
      &__title {
         display: none;
      }
   }
}
.you-need-facts {
   position: relative;
   max-width: clamp(315px, 111.5vw, 1338px);
   margin: 0 auto;
   &__box {
      position: relative;
      z-index: 1;
      width: clamp(315px, 100%, 863px);
      margin: 0 auto;
      background-color: rgba(255, 255, 255, 0.05);
      border-radius: 20px;
      padding: 66px 54px 47px 54px;

      background-color: rgb(34 17 53);
   }

   &__title {
      position: absolute;
      top: 0;
      left: 50%;
      transform: rotate(-6deg) translate(-50%, -70%);
      background: #c6f806;
      width: 220px;
      height: 65px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 138px;
      font-family: var(--font3-1);
      font-weight: 400;
      font-size: clamp(1.25rem, 1.875vw, 2.25rem);
      line-height: 127%;
      color: var(--black-text);
   }
   &__title-mobil {
      text-align: center;
      display: none;
   }

   &__text {
      font-family: var(--second-family);
      font-weight: 600;

      font-size: 2rem;
      line-height: 130%;
      text-align: center;
      color: var(--white);
      margin-bottom: 25px;
      span {
         color: #c6f806;
      }
   }

   &__subtitle {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: clamp(0.9375rem, 2vw, 1.5rem);
      text-align: center;
      color: #837d8a;
   }

   &__procent {
      position: absolute;
      z-index: 1;
      width: clamp(111px, 13.5833vw, 163px);
      height: clamp(111px, 13.5833vw, 163px);
      border-radius: 50%;
      font-family: var(--second-family);
      font-weight: 600;
      font-size: clamp(1.3125rem, 1.6667vw, 2rem);
      line-height: 130%;
      text-align: center;
      color: #c6f806;
      display: flex;
      justify-content: center;
      align-items: center;
      p {
         max-width: 73px;
      }
      svg {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
   }

   &__procent--left {
      bottom: 0;
      left: 0;
      &::after {
         content: '';
         position: absolute;
         top: 50%;
         right: 0;
         transform: translate(100%, 50%);
         width: 6.5vw;
         height: 2px;
         background-color: rgb(34 17 53);
         // background-color: red;
      }
   }

   &__procent--right {
      top: 0;
      right: 0;
      &::after {
         content: '';
         position: absolute;
         top: 50%;
         left: 0;
         transform: translate(-100%, 50%);
         width: 6.5vw;
         height: 2px;
         background-color: rgb(34 17 53);
      }
   }
   @media (max-width: 1250px) {
      &__procent {
         z-index: 1;
      }
      .circle-bg {
         opacity: 1;
         stroke: rgb(52 51 51);
      }
   }
   @media (max-width: 1470px) {
      &__procent {
         &::after {
            width: 3.5vw;
         }
      }
   }
   @media (max-width: 1370px) {
      &__procent {
         &::after {
            display: none;
         }
      }
   }
   @media (max-width: 1240px) {
      &__procent--right {
         bottom: -80px;
         top: auto;
      }
      &__procent--left {
         bottom: -80px;
      }
   }
   @media (max-width: 1120px) {
      &__text {
         font-size: clamp(1rem, 2vw, 2rem);
      }
      &__title {
         width: clamp(122px, 20.4583vw, 220px);
         height: clamp(36px, 4.6854vw, 65px);
      }
      &__box {
         padding: 40px 22px;
      }
      // &__procent--right {
      //    bottom: -80px;
      //    top: auto;
      // }
      // &__procent--left {
      //    bottom: -80px;
      // }
      // .circle-bg {
      //    opacity: 1;
      //    stroke: rgb(52 51 51);
      // }
   }
   @media (max-width: 1240px) {
      &__box {
         // margin-bottom: 170px;
         margin-bottom: 80px;
      }
   }
   @media (max-width: 650px) {
      &__title-mobil {
         display: block;
      }
   }
}

.circle-progres {
   stroke-dasharray: 511.82px;
   stroke-dashoffset: 511.82px;
   stroke-linecap: round;
   transform: rotate(-90deg);
   transform-origin: 50% 50%;
   animation-duration: 3s;
   animation-fill-mode: forwards;
   stroke: #c6f806;
}
.circle-progres--50 {
   // animation-name: circle-50;
   stroke-dashoffset: 296.82px;
}
.circle-progres--70 {
   // animation-name: circle-70;
   stroke-dashoffset: 213.82px;
}
